@font-face {
  font-family: Inter;
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@200;500&display=swap");
}

:root {
  --primary-text-color: #1e1e1e;
}

.error {
  flex-flow: column;
  margin: auto;
  display: flex;
}

.error_name {
  text-align: center;
  color: var(--primary-text-color);
  font-family: Inter, sans-serif;
  font-size: 40px;
  font-style: normal;
  font-weight: bold;
  line-height: 40px;
}

.error_comment {
  text-align: center;
  color: var(--primary-text-color);
  padding-top: 20px;
  padding-bottom: 70px;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}

.text:active, .text:hover, .text {
  color: #666;
  text-decoration: none;
}

